.NavbarItems {
    background: linear-gradient(90deg, rgb(51,51,51) 0%, rgba(51,51,51,1) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}
.navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}
.fa-user {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display:grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    align-items: stretch;
    justify-content: end;
    margin-right: 2rem;    
}

.nav-links {
    display:grid;
    color: white;
    text-decoration: none;
    padding: 14px 16px;
}

.nav-links:hover {
    display:grid;
    background-color: #04AA6D;
    color: rgb(0, 0, 0);
    padding: 14px 16px;
    border-radius: 4px;    
    transition: all 0.2s ease-out;
}


.menu-icon {
    display: none;
}

@media screen and (max-width: 960px){
    .NavbarItems {
        position: relative;
    }
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5 ease;
    }

    .nav-menu.active {
        background: rgb(51,51,51);        
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        margin-top: 0;
    }
    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }
    .nav-links:hover {
        background-color: #04AA6D;
        border-radius: 0;
        padding: 2rem;
        width: 100%;
        display: table;
    }
    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
    }
    .menu-icon {        
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times{
        color: #fff;
        font-size: 2rem;
    }
    .fa-bars{
        color: #fff;
        font-size: 2rem;
    }
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}

/* PythonLogo.css */

#python-logo {
  max-width: 20vw;
  max-height: 20vh;
  padding: 20px;
  overflow: visible;
  cursor: pointer;
}

#python-logo path {
  position: relative;
  fill: transparent;
  stroke-width: 5px;
  transition: all 0.2s ease-in-out;
}

#python-logo path:first-child {
  stroke: #3670a0;
  -webkit-animation: border-move 2s linear forwards, fill-first 1s 2s linear forwards;
          animation: border-move 2s linear forwards, fill-first 1s 2s linear forwards;
}

#python-logo path:last-child {
  stroke: #fed140;
  -webkit-animation: border-move 2s linear forwards, fill-second 1s 2s linear forwards;
          animation: border-move 2s linear forwards, fill-second 1s 2s linear forwards;
}

#python-logo:hover path:first-child {
  -webkit-transform: translate(-2%, -2%);
          transform: translate(-2%, -2%);
  text-shadow: 0 0 20px #3670a0;
}

#python-logo:hover path:last-child {
  -webkit-transform: translate(2%, 2%);
          transform: translate(2%, 2%);
  box-shadow: 0 0 20px #fed140;
}

@-webkit-keyframes border-move {
  0% {
    stroke-dasharray: 0 1600;
  }
  100% {
    stroke-dasharray: 1600 1600;
  }
}

@keyframes border-move {
  0% {
    stroke-dasharray: 0 1600;
  }
  100% {
    stroke-dasharray: 1600 1600;
  }
}

@-webkit-keyframes fill-first {
  0% {
    fill: transparent;
  }
  100% {
    fill: #3670a0;
  }
}

@keyframes fill-first {
  0% {
    fill: transparent;
  }
  100% {
    fill: #3670a0;
  }
}

@-webkit-keyframes fill-second {
  0% {
    fill: transparent;
  }
  100% {
    fill: #fed140;
  }
}

@keyframes fill-second {
  0% {
    fill: transparent;
  }
  100% {
    fill: #fed140;
  }
}
